import { Grid, makeStyles, Typography } from "@material-ui/core";
import { BigNumber } from "ethers";
import { transparentize } from "polished";
import React, { useState } from "react";
import { ILeagueDetails, IToken } from "types";

import { getLeagueStatus } from "utils/ido";
import { LeagueSection } from "utils/enums";
import { getChoicesFromHex } from "utils/league";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 16,
    backgroundPosition: "top",
    backgroundColor: theme.colors.neutralLight,
  },
  title: { color: theme.colors.neutralLighter, fontSize: 20, fontWeight: 600 },
  content: { marginTop: 8 },
  label: { color: theme.colors.neutralLighter },
}));

interface IProps {
  data: ILeagueDetails;
  choiceNumbers: BigNumber[];
}

export const StatusSection = (props: IProps) => {
  const { data, choiceNumbers } = props;
  const classes = useStyles();
  const status = getLeagueStatus(data);

  if (status === LeagueSection.Upcoming) {
    return null;
  }
  const renderChoice = (choiceStr: string) => {
    return (
      <Typography className={classes.label} component="span">
        {choiceStr}
      </Typography>
    );
  };

  return (
    <div className={classes.root}>
      {data.isFinalized && (
        <>
          <Typography className={classes.title}>Winning Number</Typography>
          <Typography className={classes.label}>
            {renderChoice(
              getChoicesFromHex(data.finalNumber, data.expectLength)
                .map((num) => data.metaData.choices[num - 1])
                .join("-")
            )}
          </Typography>

          <br />
        </>
      )}
      <Typography className={classes.title}>My Choices</Typography>
      <div className={classes.content}>
        {choiceNumbers.map((choice, index) => {
          const nums = getChoicesFromHex(choice, data.expectLength);
          return (
            <div key={`${choice.toHexString()}-${index}`}>
              <Typography className={classes.label}>
                {index + 1}.{" "}
                {renderChoice(
                  nums.map((num) => data.metaData.choices[num - 1]).join("-")
                )}
              </Typography>
            </div>
          );
        })}
      </div>
    </div>
  );
};
