import {
  Button,
  makeStyles,
  MenuItem,
  TextField,
  Typography,
} from "@material-ui/core";
import { CountDownTimer } from "components";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { useTokenBalance } from "helpers";
import { transparentize } from "polished";
import React, { useEffect, useState } from "react";
import { ILeagueDetails } from "types";
import { formatBigNumber, numberWithCommas } from "utils";
import { LeagueSection } from "utils/enums";
import { getLeagueStatus } from "utils/ido";
import moment from "moment-timezone";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 16,
    backgroundPosition: "top",
    backgroundColor: theme.colors.neutralLight,
  },
  header: { padding: 12 },
  label: {
    color: theme.colors.neutralLighter,
    fontWeight: 500,
    "& span": {
      color: theme.colors.primary,
    },
    "& + &": {
      marginTop: 8,
    },
  },
  choicesWrapper: {
    fontSize: 14,

    border: `1px solid ${transparentize(0.8, theme.colors.neutralLighter)}`,
    padding: 16,
    marginTop: 8,
    "& div": {
      "&>*+*": { marginTop: 12 },
    },
  },
  choice: { color: theme.colors.primary },
  choiceLabel: { color: theme.colors.neutralLighter },
  headerLabel: { color: theme.colors.gray2, fontSize: 14 },
  headerTimer: {
    fontSize: 24,
    fontWeight: 600,
  },
  footer: { paddingTop: 16 },
  button: {
    height: 40,

    "&:disabled": {
      backgroundColor: `${transparentize(0.4, theme.colors.gray2)} !important`,
      "& span": {
        color: `${transparentize(0.4, theme.colors.neutralLighter)} !important`,
      },
    },
  },
}));

interface IProps {
  data: ILeagueDetails;
  onBuy: (_: number[]) => Promise<void>;
  onClaim: () => Promise<void>;
  pendingRewards: BigNumber;
  personalEntriesCount: number;
}

interface IState {
  numbers: number[];
}

export const ActionSection = (props: IProps) => {
  const { data, onBuy, onClaim, pendingRewards, personalEntriesCount } = props;
  const classes = useStyles();
  const { startTime, closeTime } = data;
  const { networkId } = useConnectedWeb3Context();
  const [counter, setCounter] = useState(1);
  const increateCounter = () => setCounter((counter) => counter + 1);
  const { balance } = useTokenBalance(data.token);

  const [state, setState] = useState<IState>({
    numbers: new Array(data.expectLength).fill(1),
  });
  const status = getLeagueStatus(data);
  const token = getTokenFromAddress(data.token, networkId);
  const isDisabled = (() => {
    if (balance.lt(data.entryAmount)) {
      return true;
    }
    return false;
  })();

  useEffect(() => {
    setState((prev) => ({
      ...prev,
      numbers: new Array(data.expectLength).fill(1),
    }));
  }, [data.expectLength]);

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        {status === LeagueSection.Upcoming && (
          <div>
            <Typography className={classes.headerLabel}>Start Time:</Typography>
            <CountDownTimer
              destTimeStamp={startTime * 1000}
              onReached={increateCounter}
              className={classes.headerTimer}
            />
          </div>
        )}
        {status === LeagueSection.Ongoing && (
          <div>
            <Typography className={classes.headerLabel}>End Time:</Typography>
            <CountDownTimer
              destTimeStamp={closeTime * 1000}
              onReached={increateCounter}
              className={classes.headerTimer}
            />
          </div>
        )}
        {status === LeagueSection.Ended && (
          <Typography className={classes.headerLabel}>
            Waiting for result
          </Typography>
        )}
        {status === LeagueSection.Finalized && (
          <Typography className={classes.headerLabel}>Finalized</Typography>
        )}
      </div>
      <div>
        <Typography className={classes.label}>
          Start Time:{" "}
          <span>
            {moment(new Date(startTime * 1000))
              .utc()
              .format("MM/DD/YYYY HH:mm")}{" "}
            GMT
          </span>
        </Typography>
        <Typography className={classes.label}>
          Close Time:{" "}
          <span>
            {moment(new Date(closeTime * 1000))
              .utc()
              .format("MM/DD/YYYY HH:mm")}{" "}
            GMT
          </span>
        </Typography>
        <Typography className={classes.label}>
          Entry Price:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(data.entryAmount, token.decimals, 0)
            )}{" "}
            {token.symbol.toUpperCase()}
          </span>
        </Typography>
        <Typography className={classes.label}>
          Prize Amount:{" "}
          <span>
            {numberWithCommas(
              formatBigNumber(data.prizeAmount, token.decimals, 0)
            )}{" "}
            {token.symbol.toUpperCase()}
          </span>
        </Typography>
        {status !== LeagueSection.Upcoming && (
          <>
            <Typography className={classes.label}>
              Total entries: <span>{personalEntriesCount}</span>
            </Typography>
            <Typography className={classes.label}>
              Your entries: <span>{data.currentChoiceId.toNumber()}</span>
            </Typography>
          </>
        )}
        {status === LeagueSection.Ongoing && (
          <div className={classes.choicesWrapper}>
            <Typography className={classes.label}>
              Select Choices and Enter
            </Typography>
            <div>
              {state.numbers.map((number, index) => (
                <TextField
                  select
                  value={number}
                  key={`${index}`}
                  fullWidth
                  label={`Choice ${index + 1}`}
                  InputLabelProps={{
                    className: classes.choiceLabel,
                  }}
                  SelectProps={{
                    className: classes.choice,
                  }}
                  onChange={(event) => {
                    const newNumbers = state.numbers.map((num, nIndex) =>
                      nIndex !== index ? num : Number(event.target.value)
                    );
                    setState((prev) => ({ ...prev, numbers: newNumbers }));
                  }}
                >
                  {data.metaData.choices.map((choice, cIndex) => (
                    <MenuItem key={`${cIndex}`} value={cIndex + 1}>
                      {choice}
                    </MenuItem>
                  ))}
                </TextField>
              ))}
            </div>
          </div>
        )}
        {status === LeagueSection.Finalized && (
          <>
            <Typography className={classes.label}>
              Cliamable Amount:{" "}
              <span>
                {formatBigNumber(pendingRewards, token.decimals)} {token.symbol}
              </span>
            </Typography>
          </>
        )}
      </div>
      <div className={classes.footer}>
        {status === LeagueSection.Ongoing && (
          <div>
            <Typography className={classes.label}>
              Balance: {formatBigNumber(balance, token.decimals)} {token.symbol}
            </Typography>
            <Button
              className={classes.button}
              color="primary"
              fullWidth
              variant="contained"
              disabled={isDisabled}
              onClick={() => onBuy(state.numbers)}
            >
              Enter
            </Button>
          </div>
        )}
        {status === LeagueSection.Finalized && (
          <Button
            className={classes.button}
            color="primary"
            fullWidth
            variant="contained"
            onClick={onClaim}
            disabled={pendingRewards.isZero()}
          >
            Claim
          </Button>
        )}
      </div>
    </div>
  );
};
