import { PageWrapper, SimpleLoader } from "components";
import { IDOS_TO_HIDE } from "config/constants";
import { useLeague } from "helpers";
import { useSnackbar } from "notistack";
import { useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import { isAddress } from "utils/tools";
import { LeagueDetailsContent } from "./LeagueDetailsContent";

const LeagueDetailsPage = () => {
  const params = useParams();
  const history = useHistory();
  const id = (params as any).id;
  const { enqueueSnackbar } = useSnackbar();
  const { data, loading, load } = useLeague(id);

  useEffect(() => {
    if (
      !id ||
      !isAddress(id) ||
      IDOS_TO_HIDE.includes((id || "").toLowerCase())
    ) {
      enqueueSnackbar("Invalid league", { variant: "error" });
      history.push("/prediction-pools");
    }
  }, [id]);

  useEffect(() => {
    if (!loading && !data) {
      history.push("/prediction-pools");
    }
  }, [loading, data]);

  const renderContent = () => {
    if (!data) return null;
    return <LeagueDetailsContent data={data} onReload={load} />;
  };

  return (
    <PageWrapper>{!data ? <SimpleLoader /> : renderContent()}</PageWrapper>
  );
};

export default LeagueDetailsPage;
