import { BigNumber } from "ethers";

export const getChoicesFromHex = (choice: BigNumber, length: number) => {
  const choices = [];
  let tempChoice = choice;
  for (let index = 0; index < length; index++) {
    const ups = tempChoice.div(256).mul(256);
    const number = tempChoice.sub(ups).toNumber();
    tempChoice = tempChoice.div(256);
    choices.unshift(number);
  }
  return choices;
};
