import { Grid, makeStyles, Button } from "@material-ui/core";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";

import { BigNumber } from "ethers";
import { useConnectedWeb3Context } from "contexts";
import { ZERO } from "utils/number";
import { ILeagueDetails } from "types";
import { useIsMountedRef, useServices } from "helpers";
import LeagueAbi from "abis/League.json";
import { LEAGUE_CLIAM_SIZE_PER_TX, NULL_ADDRESS } from "config/constants";
import { calculateGasMargin } from "utils";

import { ActionSection, OverviewSection, StatusSection } from "./components";
import { LeagueService } from "services/league";
import { ERC20Service } from "services/erc20";

import { useHistory } from "react-router-dom";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
const useStyles = makeStyles((theme) => ({
  content: { padding: "32px 16px" },
  mainContainer: {
    marginTop: 16,
    marginBottom: 16,
  },
}));

interface IProps {
  data: ILeagueDetails;
  onReload: () => Promise<void>;
}

interface IState {
  personalEntriesCount: number;
  pendingRewards: BigNumber;
  choiceNumbers: BigNumber[];
  choiceIds: BigNumber[];
  choiceStatuses: boolean[];
}

const initialState: IState = {
  pendingRewards: ZERO,
  personalEntriesCount: 0,
  choiceNumbers: [],
  choiceIds: [],
  choiceStatuses: [],
};

export const LeagueDetailsContent = (props: IProps) => {
  const {
    account,
    library: provider,
    networkId,
    setTxModalInfo,
    setWalletConnectModalOpened,
  } = useConnectedWeb3Context();
  const [state, setState] = useState<IState>(initialState);
  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  const { data, onReload } = props;
  const { enqueueSnackbar } = useSnackbar();
  const { multicall } = useServices();

  let history = useHistory();
  const handleClick = () => history.push("/prediction-pools");
  const loadPersonalInfo = async () => {
    if (!account || !networkId) {
      setState((prev) => initialState);
    }

    try {
      const calls = [
        {
          name: "getUserChoiceIds",
          address: data.address,
          params: [account, 0, LEAGUE_CLIAM_SIZE_PER_TX],
        },
        {
          name: "pendingRewards",
          address: data.address,
          params: [account],
        },
        {
          name: "getUserChoiceCount",
          address: data.address,
          params: [account],
        },
      ];
      const [
        [choiceIds, choiceNumbers, statuses],
        [pendingRewards],
        [personalEntriesCount],
      ] = await multicall.multicallv2(LeagueAbi, calls);

      if (isMountedRef.current === true) {
        setState((prev) => ({
          ...prev,
          pendingRewards,
          personalEntriesCount: personalEntriesCount.toNumber(),
          choiceIds,
          choiceStatuses: statuses,
          choiceNumbers,
        }));
      }
    } catch (error) {
      if (isMountedRef.current === true) setState((prev) => initialState);
    }
  };

  useEffect(() => {
    loadPersonalInfo();
  }, [account, networkId]);

  const onClaim = async () => {
    if (!account || !provider) {
      setWalletConnectModalOpened(true);
      return;
    }
    try {
      const leagueService = new LeagueService(provider, account, data.address);
      const ids = [];
      for (let index = 0; index < state.choiceStatuses.length; index++) {
        if (!state.choiceStatuses[index]) {
          ids.push(state.choiceIds[index]);
        }
      }
      setTxModalInfo(true, "Claiming...");
      const hash = await leagueService.claim(ids);
      setTxModalInfo(
        true,
        "Claiming...",
        "Please wait until tx is confirmed",
        hash
      );
      await provider.waitForTransaction(hash);
      enqueueSnackbar("You've cliamed successfully!");
      setTxModalInfo(true, "Reloading...");
      await loadPersonalInfo();
      await onReload();

      setTxModalInfo(false);
    } catch (error: any) {
      setTxModalInfo(false);
      enqueueSnackbar(error.message || "Something went wrong", {
        variant: "error",
      });
    }
  };

  const onBuy = async (nums: number[]) => {
    if (!account || !provider) {
      setWalletConnectModalOpened(true);
      return;
    }
    try {
      setTxModalInfo(true, "Checking allowance...");
      const tokenService = new ERC20Service(provider, account, data.token);
      const hasEnoughAllowance = await tokenService.hasEnoughAllowance(
        account,
        data.address,
        data.entryAmount
      );
      if (!hasEnoughAllowance) {
        setTxModalInfo(true, "Approving...");
        const hash = await tokenService.approveUnlimited(data.address);
        setTxModalInfo(
          true,
          "Approving...",
          "Please wait until tx is confirmed",
          hash
        );
        await provider.waitForTransaction(hash);
      }
      const leagueService = new LeagueService(provider, account, data.address);
      setTxModalInfo(true, "Entering...");
      const hash = await leagueService.enterChoice(nums);
      setTxModalInfo(
        true,
        "Entering...",
        "Please wait until tx is confirmed",
        hash
      );
      await provider.waitForTransaction(hash);
      enqueueSnackbar("You've entered successfully!");
      setTxModalInfo(true, "Reloading...");
      await loadPersonalInfo();
      await onReload();

      setTxModalInfo(false);
    } catch (error: any) {
      setTxModalInfo(false);
      enqueueSnackbar(error.message || "Something went wrong", {
        variant: "error",
      });
    }
  };

  return (
    <div className={classes.content}>
      <Button color="primary" onClick={handleClick} variant="text">
        <ChevronLeftIcon fontSize="small" />
        Go back
      </Button>
      <Grid container spacing={2} className={classes.mainContainer}>
        <Grid item xs={12} md={7}>
          <OverviewSection data={data} />
        </Grid>
        <Grid item xs={12} md={5}>
          <ActionSection
            data={data}
            pendingRewards={state.pendingRewards}
            onBuy={onBuy}
            onClaim={onClaim}
            personalEntriesCount={state.personalEntriesCount}
          />
        </Grid>
      </Grid>
      <Grid container spacing={2} className={classes.mainContainer}>
        <Grid item sm={12} md={12}>
          {state.choiceNumbers.length && (
            <Grid item sm={12}>
              <StatusSection data={data} choiceNumbers={state.choiceNumbers} />
            </Grid>
          )}
        </Grid>
      </Grid>
    </div>
  );
};
