import { Grid, makeStyles, Typography } from "@material-ui/core";
import { LazyBg, LazyImage } from "components";
import React from "react";
import { ILeagueDetails } from "types";
import DeveloperModeIcon from "@material-ui/icons/DeveloperMode";
import ContentCopyIcon from "@material-ui/icons/FileCopy";
import copy from "copy-to-clipboard";

import { useConnectedWeb3Context } from "contexts";
import { getEtherscanUri } from "config/networks";
import { shortenAddress } from "utils";
import { transparentize } from "polished";
import { useSnackbar } from "notistack";

const avatarSize = 100;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "flex-start",
    padding: 16,
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
    },
    [theme.breakpoints.down("sm")]: {
      padding: 0,
    },
    backgroundColor: theme.colors.neutralLight,
  },
  content: { padding: 16, flex: 1 },
  logoWrapper: {
    marginRight: 16,
    // backgroundColor: theme.colors.primary,
    borderRadius: "50%",
    [theme.breakpoints.down("xs")]: {
      alignSelf: "center",
    },
  },
  logo: {
    borderRadius: "50%",
    width: 60,
    height: 60,
  },
  title: {
    color: theme.colors.neutralLighter,
    fontSize: 24,
    fontWeight: 600,
  },

  header: {
    display: "flex",
    alignItems: "flex-start",
    marginBottom: 16,
  },
  description: { color: theme.colors.neutralLighter, fontSize: 14 },
  bottom: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    paddingTop: 16,
    [theme.breakpoints.down("sm")]: {
      flexDirection: "column",
      alignItems: "flex-start",
    },
  },
  contractWrapper: {
    display: "flex",
    alignItems: "center",
    "& div": {
      color: theme.colors.primary,
      padding: 2,

      backgroundColor: transparentize(0.8, theme.colors.primary),
      "& svg": {
        height: 18,
        width: 18,
      },
    },
    "& span": {
      color: theme.colors.neutralLighter,
    },
    "& a": { color: theme.colors.primary, padding: 2, textDecoration: "none" },
  },
  copy: {
    cursor: "pointer",
    transition: "all 0.4s",
    "&:hover": {
      opacity: 0.7,
    },
    "& svg": {
      height: 18,
      width: 18,
    },
  },
  warn: {
    color: "#a00",
    marginTop: 4,
  },
  choices: { marginTop: 24 },
  choiceTitle: { color: theme.colors.neutralLighter, marginBottom: 8 },
  choice: {
    color: theme.colors.neutralLighter,
    padding: "2px 4px",

    fontSize: 14,
  },
}));

interface IProps {
  data: ILeagueDetails;
}

export const OverviewSection = (props: IProps) => {
  const {
    data: {
      address,
      metaData: { banner, title, description, choices, external },
    },
  } = props;
  const classes = useStyles();
  const { networkId } = useConnectedWeb3Context();
  const etherscanUri = getEtherscanUri(networkId);
  const { enqueueSnackbar } = useSnackbar();

  const onCopy = () => {
    copy(address);
    enqueueSnackbar("Copied to clipboard");
  };

  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <div className={classes.header}>
          <LazyImage
            src={banner}
            imgClassName={classes.logo}
            className={classes.logoWrapper}
          />
          <div>
            <Typography className={classes.title}>{title}</Typography>
            <Typography className={classes.description}>
              {description}
            </Typography>
          </div>
        </div>

        <div className={classes.bottom}>
          <div>
            <div className={classes.contractWrapper}>
              <div>
                <DeveloperModeIcon />
              </div>
              &nbsp;
              <span>Contract:</span>&nbsp;
              <a
                href={`${etherscanUri}address/${address}`}
                target="_blank"
                rel="noreferrer"
              >
                {shortenAddress(address)}
              </a>
              &nbsp;
              <span className={classes.copy} onClick={onCopy}>
                <ContentCopyIcon />
              </span>
            </div>
            <Typography className={classes.warn}>
              Don't send money to this address
            </Typography>
          </div>
        </div>
        <div className={classes.choices}>
          <Typography className={classes.choiceTitle}>
            Possible Choices
          </Typography>
          <Grid container spacing={2}>
            {choices.map((choice, index) => (
              <Grid item xs={6} sm={4} md={3} key={`${choice}-${index}`}>
                <Typography className={classes.choice}>
                  {index + 1}. {choice}
                </Typography>
              </Grid>
            ))}
          </Grid>
        </div>
      </div>
    </div>
  );
};
