import { BigNumber, Contract, Wallet, ethers } from "ethers";
import { Maybe } from "types";
import LeagueAbi from "abis/League.json";

class LeagueService {
  provider: any;
  contract: Contract;

  constructor(provider: any, signerAddress: Maybe<string>, addr: string) {
    this.provider = provider;
    if (signerAddress) {
      const signer: Wallet = provider.getSigner();
      this.contract = new ethers.Contract(addr, LeagueAbi, provider).connect(
        signer
      );
    } else {
      this.contract = new ethers.Contract(addr, LeagueAbi, provider);
    }
  }

  get address(): string {
    return this.contract.address;
  }
  async enterChoice(ids: number[]) {
    const transactionObject = await this.contract.enterChoice(ids);
    console.log(`enterChoice transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }
  async claim(ids: BigNumber[]) {
    const transactionObject = await this.contract.claim(ids);
    console.log(`claim transaction hash: ${transactionObject.hash}`);
    return transactionObject.hash;
  }
}

export { LeagueService };
